    <p> 16.3 本协议条款变更后，如果用户继续使用我抓科技提供的软件或服务，即视为用户已接受修改后的协议。如果用户不接受修改后的协议，应当停止使用我抓科技提供的软件或服务。</p>

    <h2>十七、【服务的变更、中断、终止】</h2>
    <p>17.1 我抓科技可能会对服务内容进行变更，也可能会中断、中止或终止服务。</p>
    <p>（1）根据法律规定用户应提交真实信息，而用户提供的个人资料不真实、或与注册时信息不一致又未能提供合理证明；</p>
    <p>（2）用户违反相关法律法规或本协议的约定；</p>
    <p>（3）按照法律规定或主管部门的要求；</p>
    <p>（4）出于安全的原因或其他必要的情形。</p>

    <p>17.3 我抓科技有权按本协议8.2条的约定进行收费。若用户未按时足额付费，我抓科技有权中断、中止或终止提供服务。</p>

    <p>17.4 用户有责任自行备份存储在本服务中的数据。如果用户的服务被终止，我抓科技可以从服务器上永久地删除用户的数据。服务终止后，我抓科技没有义务向用户返还数据。</p>

    <h2>十八、【管辖与法律适用】</h2>
    <p>18.1 本协议签订地为中华人民共和国广东省深圳市宝安区。</p>

    <p>18.2 本协议的成立、生效、履行、解释及纠纷解决，适用中华人民共和国大陆地区法律（不包括冲突法）。</p>

    <p>18.3 若用户和我抓科技之间发生任何纠纷或争议，首先应友好协商解决；协商不成的，用户同意将纠纷或争议提交本协议签订地有管辖权的人民法院管辖。</p>

    <p>18.4 本协议所有条款的标题仅为阅读方便，本身并无实际涵义，不能作为本协议涵义解释的依据。</p>

    <p>18.5 本协议条款无论因何种原因部分无效或不可执行，其余条款仍有效，对双方具有约束力。</p>

    <h2>十九、【未成年人使用条款】</h2>
    <p>19.1 若用户未满18周岁，则为未成年人，应在监护人监护、指导下阅读本协议和使用本服务。</p>

    <p>19.2 未成年人用户涉世未深，容易被网络虚象迷惑，且好奇心强，遇事缺乏随机应变的处理能力，很容易被别有用心的人利用而又缺乏自我保护能力。因此，未成年人用户在使用本服务时应注意以下事项，提高安全意识，加强自我保护：</p>
    <p>（1）认清网络世界与现实世界的区别，避免沉迷于网络，影响日常的学习生活；</p>
    <p>（2）填写个人资料时，加强个人保护意识，以免不良分子对个人生活造成骚扰；</p>
    <p>（3）在监护人或老师的指导下，学习正确使用网络；</p>
    <p>（4）避免陌生网友随意会面或参与联谊活动，以免不法分子有机可乘，危及自身安全。</p>

    <p>19.3 监护人、学校均应对未成年人使用本服务时多做引导。特别是家长应关心子女的成长，注意与子女的沟通，指导子女上网应该注意的安全问题，防患于未然。</p>
